/* ContactUs.css */

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.map-container {
  position: relative;
  overflow: hidden;
  padding-bottom: 56.25%;
  height: 0;
}

.map-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.row {
  margin: 20px 0;
}

.icondiv {
  font-size: 24px;
  margin-bottom: 10px;
}

.contactptag {
  font-weight: bold;
}

.contactptag2,
.contactname,
.textarea,
.require {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
}

.mybtn {
  background-color: #28a745;
  color: #fff;
  display: flex;
  justify-content: space-evenly;
  padding: 10px 20px;
  margin-left: 23px;
  border: none;
  width: 200px;
  cursor: pointer;
  font-size: 16px;
  box-shadow: 10px 10px 20px rgb(190, 187, 187) ;
}