/* Footer.css */

.footer-container {
    background-color: #343a40;
    color: #fff;
    padding: 30px;
  }
  
  .footer-title {
    text-align: center;
  }
  
  .contact-info {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .contact-item {
    margin-right: 20px;
  }
  
  .contact-link {
    color: #fff;
    text-decoration: none;
  }
  
  .download-section {
    display: flex;
    justify-content: space-around;
    margin-top: 30px;
  }
  
  .store-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .store-logo {
    width: 80px;
    height: 80px;
  }
  
  .store-info {
    margin-top: 10px;
    text-align: center;
  }
  
  .download-text {
    font-weight: bold;
    display: block;
  }
  
  .store-link {
    color: #fff;
    text-decoration: none;
  }
  
  .footer-links {
    text-align: center;
    margin-top: 30px;
  }
  
  .copyright-text {
    margin: 0;
  }
  
  .footer-link {
    color: #fff;
    text-decoration: none;
    margin-right: 10px;
  }
  