.bgc13 {
    height: 300px; /* Adjust the height as needed */
    background-size: cover;
    background-position: center;
  }

  /* Text styles */
  .text {
    color: #333;
    font-size: 1.2rem;
  }

  .text-2 {
    color: #555;
    font-size: 1.5rem;
  }

  .text-3 {
    color: #777;
  }

  /* Carousel caption styles */
  .carousel-caption {
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    padding: 10px;
  }

  .checkpoint,
  .owntext {
    color: #fff;
    font-size: 2rem;
  }

  /* Solution section styles */
  .solution {
    color: #333;
    font-size: 1.5rem;
  }

  /* Container adjustments */
  .container-fluid.main {
    padding: 20px;
  }

  /* Meter card styles */
  .mb-4 {
    margin-bottom: 20px;
  }

  /* Industries section styles */
  .mile9,
  .mile10 {
    width: 100%;
    height: auto;
    max-width: 150px; /* Adjust the size as needed */
  }
  .indistury{
    background-color: skyblue;color: rgb(127, 111, 111);
  }