.mybutton{
  height: 45px;
  width: 250px;
  background-color: rgba(214, 41, 41, 0.515);
  border: none;
  font-weight: bolder;
  color: black;
  font-weight: 700;
  transition: all ease 1s;
  border-radius: 5px;
}
.mybutton:hover{
  background-color: firebrick;
  letter-spacing: 2px;
  color: white;
  font-weight: 700;
}
.mybutton2{
  background-color: gray;
  border: none;
  height: 45px;
  width: 250px;
  color: black;
  font-weight: 700;
  transition: all ease 0.6s;
  border-radius: 5px;
}
.mybutton2:hover{
  background-color: rgb(58, 52, 52);
  letter-spacing: 2px;
  color: white;
  font-weight: 700;
}
