/* Custom styles for responsiveness and attractiveness */

/* Header styles */
.header {
    background-color: #333;
    color: white;
    padding: 15px;
  }
  
  /* Container styles */
  .container-fluid.about {
    padding: 20px;
  }
  
  /* Our Legacy section styles */
  .our-lagacy {
    color: #333;
  }
  
  /* Director section styles */
  .dir {
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  /* Quality Assurance section styles */
  .our-lagacyy {
    color: #333;
  }
  
  /* Infrastructure section styles */
  .our-lagacyy {
    color: #333;
  }
  
  /* Vision & Mission section styles */
  .our-lagacyy {
    color: #333;
  }
  
  /* Footer styles */
  .footer {
    background-color: #333;
    color: white;
    padding: 15px;
  }
  
  /* Responsive adjustments */
  @media (max-width: 767px) {
    /* Adjustments for smaller screens */
  
    /* Center the D-Tech Solutions heading */
    .our-lagacy.text-center {
      text-align: center !important;
    }
  
    /* Margin adjustments for images */
    .dir,
    .img-fluid {
      margin-bottom: 15px;
    }
  
    /* Padding adjustments for the container */
    .container-fluid.about {
      padding: 10px;
    }
  }