/* ThreePhase.css */

/* Banner */
.threephasebanner {
    width: 100%;
    height: auto;
  }
  
  /* Main Image */
  .threeph {
    width: 100%;
    height: auto;
    border: 1px solid #ddd; /* Add a border for better separation */
  }
  
  /* Secondary Image */
  .threeph2 {
    width: 100%;
    height: auto;
    border: 1px solid #ddd; /* Add a border for better separation */
  }
  
  /* Text and Information Section */
  .threephcol {
    color: #333;
    margin-top: 20px;
  }
  
  /* Styling for individual information points */
  .threespan {
    font-weight: bold;
    color: #007bff;
  }
  
  /* Responsive adjustments */
  @media (max-width: 767px) {
    .threephasebanner {
      height: 200px; /* Adjust the height for smaller screens */
    }
  
    .threeph {
      margin-top: 20px; /* Add margin for better spacing */
    }
  }
  
  /* Hide overflow on smaller screens */
  @media (max-width: 576px) {
    body {
      overflow-x: hidden;
    }
  
    .threephcol {
      margin-top: 10px;
    }
  }
  
